import Vue from 'vue';

import template from 'raw-loader!./template.html';
import './style.css';

export default class Tooltip {
  constructor(data) {
    const el = document.createElement('div');
    this.vm = new Vue({
      el,
      template,
      data: {
        year: null,
        ratio: null,
        population: null,
        count: null,
        prefix: 're-tooltip',
        ...data,
      },
    });
  }

  render(data) {
    Object.assign(this.vm, {
      ...data,
      count: Math.ceil((data.population / this.vm.base) * 10),
    });
    this.prevData = data;
    return this.vm.$el.outerHTML;
  }
}



