<template>
  <legend-base>
    <span slot="legend" class="legend-line" />
    {{label}}
  </legend-base>
</template>

<script>
import LegendBase from './LegendBase';

export default {
  components: {
    LegendBase,
  },
  props: {
    label: String,
  },
  name: 'legend-line',
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.legend-line {
  display: block;
  background: black;
  height: 2px;
}
</style>



