<template>
  <div></div>
</template>

<script>
import c3 from 'c3';
import 'c3/c3.css';

export default {
  name: 'c3-line',
  props: {
    options: Object,
    data: {
      type: Object,
      required: true,
    },
    columns: {
      type: Object,
      required: true,
    },
    width: [Number, String],
    height: [Number, String],
    delay: {
      type: Number,
      default: 10,
    },
  },
  mounted() {
    const {
      data,
      options,
      delay,
      columns,
      width,
      height,
    } = this;

    // const currentWidth = this.$el.offsetWidth;

    const setUp = {
      bindto: this.$el,
      data,
      ...options,
    };

    if (width && height) {
      Object.assign(setUp, {
        size: {
          width,
          height,
        },
        oninit: () => {
          this.$el.style.width = '100%';
          this.$el.style.height = '100%';
          this.$el.style.maxHeight = 'none';
          this.svgEle = this.$el.querySelector('svg');
          this.svgEle.setAttribute('viewBox', `0 0 ${width} ${height}`);
          this.svgEle.style.width = '100%';
        },
        onrendered: () => {
          this.$el.style.maxHeight = 'none';
          this.svgEle.removeAttribute('width');
          this.svgEle.removeAttribute('height');
        },
        onresized: () => {
          // this.$el.setAttribute('style', 'position: relative');
        },
      });
    }

    this.chart = c3.generate(setUp);

    const realData = Object.keys(columns).map(key => [key, ...columns[key]]);
    let n = 1;

    const stepByStep = () => {
      if (n <= realData[0].length) {
        setTimeout(() => {
          this.chart.load({
            columns: realData.map(list => list.slice(0, n)),
          });
          n += 1;
          stepByStep();
        }, delay);
      }
    };
    stepByStep();
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.c3-area {
  opacity: 0.7 !important;
}

.c3 svg {
  font-family: 'PingFangTC-Medium', 'Lantinghei TC', 'Microsoft Jhenghei', sans-serif;
  font-size: 0.75rem;
}
</style>



