<template>
  <div class="container">
    <div class="chart-title">
      <h2>12歲以上上網率及上網人口</h2>
      <legend-line label="上網率" />
      <legend-rect
        label="上網人口"
        :color="color"
      />
    </div>
    <line-chart
      :columns="columns"
      :data="data"
      :options="options"
    />
  </div>
</template>

<script>
import netUser from '@/data/net_user.json';
import LineChart from './c3/LineChart';
import Tooltip from './c3/Tooltip';
import LegendRect from './legend/LegendRect';
import LegendLine from './legend/LegendLine';

const ratioLabel = '上網率(%)';
const populationLabel = '上網人口數(萬人)';

const color = 'rgb(255, 233, 141)';

const tooltip = new Tooltip({
  base: 1800,
  color: '#E59A38',
});

export default {
  components: {
    LineChart,
    LegendRect,
    LegendLine,
  },
  name: 'net-user',
  data() {
    return {
      color,
      columns: {
        population: netUser.map(d => d.population),
        ratio: netUser.map(d => d.rate),
      },
      data: {
        x: 'year',
        columns: [
          ['year'].concat(netUser.map(d => d.year)),
          ['population', netUser[0].population],
          ['ratio', netUser[0].rate],
        ],
        axes: {
          ratio: 'y',
          population: 'y2',
        },
        colors: {
          ratio: 'black',
          population: color,
        },
        types: {
          ratio: 'line',
          population: 'area',
        },
        classes: {
          ratio: 'network-ratio',
          population: 'network-population',
        },
      },
      options: {
        padding: {
          top: 10,
        },
        legend: {
          show: false,
        },
        axis: {
          x: {
            label: {
              text: '年份(民國)',
              position: 'outer-center',
            },
            tick: {
              culling: false,
            },
            padding: {
              left: 0,
              right: 0,
            },
            min: netUser[0].year,
            max: netUser[netUser.length - 1].year,
          },
          y: {
            label: {
              text: ratioLabel,
              position: 'outer-middle',
            },
            padding: {
              bottom: 0,
              top: 0,
            },
            min: 55,
            max: 80,
            tick: {
              values: [60, 65, 70, 75, 80],
            },
          },
          y2: {
            show: true,
            label: {
              text: populationLabel,
              position: 'outer-middle',
            },
            padding: {
              bottom: 0,
              top: 0,
            },
            min: 0,
            max: 2000,
            tick: {
              count: 5,
            },
          },
        },
        point: {
          r: 0,
          focus: {
            expand: {
              enabled: true,
              r: 4,
            },
          },
        },
        grid: {
          lines: {
            front: true,
          },
        },
        transition: {
          duration: 500,
        },
        tooltip: {
          contents: (d) => {
            const data = d.reduce((obj, dd) => ({
              ...obj,
              [dd.id]: dd.value,
              year: dd.x,
            }), {});

            return tooltip.render(data);
          },
        },
      },
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.c3-target-network-ratio > .c3-lines > .c3-line {
  stroke-width: 3px;
}
.c3-target-network-population .c3-line {
  stroke: black !important;
  stroke-width: 1px;
}
.c3-target-network-population .c3-circle {
  fill: black !important;
}
.c3 path.domain {
  stroke-width: 2px;
}
</style>

<style scoped>

.container {
  padding: 2em;
}

.chart-title {
  text-align: right;
  border-bottom: 1px solid black;
  padding-bottom: 2rem;
  margin-bottom: 1rem;
}

.chart-title h2 {
  font-size: 2em;
  text-align: left;
  letter-spacing: 0.25em;
  margin-right: -0.25em;
}
</style>



