<template>
  <legend-base>
    <span slot="legend" class="legend-rect" :style="{ background: color }" />
    {{label}}
  </legend-base>
</template>

<script>
import LegendBase from './LegendBase';

export default {
  components: {
    LegendBase,
  },
  props: {
    label: String,
    color: String,
  },
  name: 'legend-rect',
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.legend-rect {
  display: block;
  height: 1em;
}
</style>



