<template>
  <span class="legend-wrapper">
    <span class="legend">
      <slot name="legend"></slot>
    </span>
    <span class="legend-label">
      <slot></slot>
    </span>
  </span>
</template>

<script>

export default {
  name: 'legend-base',
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.legend-wrapper {
  display: inline-block;
  margin-left: 1rem;
}

.legend {
  width: 3em;
  display: inline-block;
  border: 1px solid black;
  margin: 0 1rem;
  vertical-align: middle;
}

</style>



