<template>
  <div class="container">
    <div class="chart-container font-round" :style="containerStyle">
      <div :style="chartStyle">
        <line-chart
          :columns="columns"
          :data="data"
          :options="options"
          :width="chartLayer.width"
          :height="chartLayer.height"
        />
      </div>
      <div v-for="layer in layers" :style="layer">
        <div v-if="layer.name === 'bubble'" class="bubble-title font-round">個人上網率</div>
      </div>
    </div>
  </div>
</template>

<script>
import netUser from '@/data/net_user.json';
import LineChart from './c3/LineChart';
import graphicalData from './graphicalData';

const ratioLabel = '上網率(%)';

export default {
  components: {
    LineChart,
  },
  name: 'home',
  data() {
    return {
      columns: {
        ratio: netUser.map(d => d.rate),
      },
      data: {
        x: 'year',
        columns: [
          ['year'].concat(netUser.map(d => d.year)),
          ['ratio', netUser[0].rate],
        ],
        axes: {
          ratio: 'y',
        },
        colors: {
          ratio: 'black',
        },
        types: {
          ratio: 'spline',
        },
        classes: {
          ratio: 'graphical-network-ratio',
        },
      },
      options: {
        legend: {
          show: false,
        },
        padding: {
          top: 10,
          right: 15,
          bottom: 10,
        },
        axis: {
          x: {
            label: {
              text: '年份(民國)',
              position: 'outer-right',
            },
            tick: {
              culling: false,
            },
            padding: {
              left: 0,
              right: 0,
            },
            min: netUser[0].year,
            max: netUser[netUser.length - 1].year,
          },
          y: {
            label: {
              text: ratioLabel,
              position: 'outer-top',
            },
            inner: true,
            padding: {
              bottom: 0,
              top: 0,
            },
            min: 60,
            max: 80,
            tick: {
              values: [65, 70, 75, 80],
            },
          },
        },
        point: {
          r: 0,
          focus: {
            expand: {
              enabled: true,
              r: 4,
            },
          },
        },
        grid: {
          lines: {
            front: true,
          },
        },
        transition: {
          duration: 500,
        },
        tooltip: {
          contents: (d) => {
            const data = d.reduce((obj, dd) => ({
              ...obj,
              [dd.id]: dd.value,
              year: dd.x,
            }), {});

            return `
              <div class="g-tooltip-container font-round">
                ${data.year}年：${data.ratio}%
              </div>
            `;
          },
        },
      },
      chartStyle: graphicalData.chart,
      chartLayer: graphicalData.chartLayer,
      layers: graphicalData.layers,
      containerStyle: {
        paddingTop: graphicalData.aspectRatio,
      },
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.c3-target-graphical-network-ratio > .c3-lines > .c3-line {
  stroke-width: 4px;
}

.chart-container .c3-xgrid-focus {
  stroke-width: 2px;
}

.c3 path.domain {
  stroke-width: 2px;
}

.font-round svg {
  font-family: 'cwTeXYen', sans-serif !important;
  font-size: 1em !important;
}

.g-tooltip-container {
  background: white;
  padding: 0.5rem;
  border: 3px solid black;
  font-size: 1.25em;
  border-radius: 0.5rem;
}
</style>

<style scoped>
.container {
  margin: 2em;
  background: #F9F2D4;
}

.chart-container {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.bubble-title {
  position: absolute;
  top: 42%;
  width: 100%;
  transform: translateY(-50%);
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  letter-spacing: 0.125em;
}

</style>



