import d3 from 'd3';

import images from './images';
import layerData from './data.json';

const percentage = d3.format('.2%');

const {
  width,
  height,
} = layerData;

const layers = layerData.layers.slice();

const chartIndex = layers.findIndex(d => d.name === 'chart');
const chartLayer = layers.splice(chartIndex, 1)[0];

const styleMapper = (attr, bg) => {
  const style = {
    top: attr.y / height,
    left: attr.x / width,
    width: attr.width / width,
    height: attr.height / height,
  };
  const keys = Object.keys(style);
  const baseStyle = Object.assign({}, {
    position: 'absolute',
    name: attr.name,
  }, bg && {
    backgroundImage: `url(${bg})`,
    pointerEvents: 'none',
  });

  return keys.reduce((obj, key) =>
      Object.assign({}, obj, { [key]: percentage(style[key]) })
    , baseStyle);
};

export default {
  width,
  height,
  aspectRatio: percentage(height / width),
  chart: styleMapper(chartLayer),
  chartLayer,
  layers: layers.map(l => styleMapper(l, images[l.name])),
};



